.answer_item {
    margin: 10px auto;
    padding: 5px;
    width: 270px;
    height: 55px;
    z-index: 3;
    position: relative;

    display: grid;
    grid-template-columns: 1fr;

    background: #FFFF;
    border: 1px solid #588EFF;
    box-sizing: border-box;
    border-radius: 25px;
    text-align: center;
    color: #000000;
}

.answer_item:active {
    background: #5F92FD;
    color: #FFFF;
}

.number {
    margin-left: 15px;
  
    font-family: Titillium Web, sans-serif;
    font-style: normal;
    font-weight: bold;
    font-size: 18px;
    line-height: 21px;
    display: flex;
    align-items: center;
    letter-spacing: -0.04em;

}

.text {
    margin: auto;
    font-family: Titillium Web, sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 16px;
    display: flex;
    align-items: center;
    letter-spacing: -0.04em;
}

@media screen and (min-device-height: 750px) {
    .answer_item {
        margin: 10px auto;
    }
}